<template>
  <base-layout :is-header-showed="!$vuetify.breakpoint.mobile">
    <template #header>
      <h1 class="font-weight-bold primary-dark--text">
        {{ $route.meta.pageTitle }}
      </h1>
    </template>

    <template #content>
      <div class="content-page-wrapper d-flex flex-column flex-md-row flex-shrink-1 px-lg-6 py-lg-8 px-0 py-0 my-md-0 mx-lg-n2 mx-md-auto">
        <v-card
          :width="$vuetify.breakpoint.smAndDown ? 'auto' : '100%'"
          :height="$vuetify.breakpoint.mobile ? '100%' : 'fit-content'"
          class="py-lg-5 py-md-12 px-5 pt-5 pb-7 mx-lg-2 mx-md-2 mx-0 px-lg-5 px-2 flex-grow-1"
        >
          <div class="form-wrapper">
            <v-form
              ref="form"
              v-model="form.valid"
              class="form"
              lazy-validation
              @keyup.enter.native="handleSubmit"
            >
              <base-field-input
                label="Current password"
                          :is-required="true"
                v-model="form.values.password"
                autocomplete="current-password"
                :rules="[required('Current password')]"
                :type="isPasswordVisible ? 'text' : 'password'"
                :append-icon="isPasswordVisible ? 'mdi-eye' : 'mdi-eye-off'"
                :error-messages="form.serverError"
                @click:append="isPasswordVisible = !isPasswordVisible"
              />

              <base-field-input
                label="New password"
                          :is-required="true"
                v-model="form.values.new_password"
                autocomplete="new-password"
                :rules="[required('New password')]"
                :type="isNewPasswordVisible ? 'text' : 'password'"
                :append-icon="isNewPasswordVisible ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="isNewPasswordVisible = !isNewPasswordVisible"
                @input="handleNewPasswordInput"
              />

              <base-field-input
                label="Confirm new password"
                          :is-required="true"
                v-model="form.values.confirm_new_password"
                autocomplete="confirm-new-password"
                :rules="[required('Confirm new password'), sameAsNewPassword]"
                :type="isConfirmNewPasswordVisible ? 'text' : 'password'"
                :append-icon="isConfirmNewPasswordVisible ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="isConfirmNewPasswordVisible = !isConfirmNewPasswordVisible"
              />
            </v-form>

            <v-btn
              :height="$vuetify.breakpoint.mobile ? 50 : 35"
              :disabled="!form.valid"
              depressed
              color="primary"
              class="mt-2"
              @click="handleSubmit"
            >
              Change Password
            </v-btn>
          </div>
        </v-card>
      </div>
    </template>
  </base-layout>
</template>

<script>
import { createNamespacedHelpers, mapState } from 'vuex';
import { actions } from '@modules/profile/constants';
import BaseFieldInput from '@components/BaseFieldInput';
import BaseLayout from '@components/BaseLayout';
import { required, same } from '@utils/helpers/validationRules';

const { mapActions } = createNamespacedHelpers('profile');

export default {
  name: 'ChangePassword',
  components: {
    BaseFieldInput,
    BaseLayout,
  },
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.form.serverError = '';
      },
    },
  },
  data() {
    return {
      form: {
        valid: true,
        values: {
          confirm_new_password: '',
          new_password: '',
          password: '',
        },
        serverError: '',
      },
      isConfirmNewPasswordVisible: false,
      isNewPasswordVisible: false,
      isPasswordVisible: false,
    };
  },
  computed: {
    sameAsNewPassword() {
      return this.same('Passwords', this.form.values.new_password);
    },
    ...mapState(['userRole']),
  },
  methods: {
    required,
    same,
    handleSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;
      // eslint-disable-next-line
      const { confirm_new_password, new_password, password } = this.form.values;
      this.changePassword({
        confirm_new_password,
        new_password,
        password,
        role: this.userRole,
      })
        .then(() => {
          this.$notificationSuccess('Your password was changed');
          if (this.userRole === this.$constants.ROLES.CARRIER) {
            this.$router.push({ name: this.$constants.ROUTE_NAMES.PROFILE });
          } else {
            this.$router.push({ name: this.$constants.ROUTE_NAMES.DISPATCHER_PROFILE_INFO });
          }
        }).catch((errorMessage) => {
          this.form.serverError = errorMessage;
        });
    },
    handleNewPasswordInput() {
      setTimeout(() => {
        this.$refs.form.inputs[2].validate();
      });
    },
    ...mapActions({
      changePassword: actions.UPDATE_PASSWORD,
    }),
  },
};
</script>
